<template>
  <div class="questionnaire">
    <form class="container border-dark bg-transparent">
      <div class="mb-3">
        <label for="userName" class="form-label">UserName</label>
        <input type="text" class="form-control" id="exampleInputPassword1" v-model="userName">
      </div>
      <div class="mb-3">
        <label for="userName" class="form-label">Password</label>
        <input type="text" class="form-control" id="exampleInputPassword1" v-model="password">
      </div>
      <div class="mb-3">
        <label for="email" class="form-label">Email</label>
        <input type="text" class="form-control" id="email" v-model="email">
      </div>
      <div class="mb-3">
        <label for="exampleInputPassword1" class="form-label">Phone</label>
        <input type="text" class="form-control" id="exampleInputPassword1" v-model="phone">
      </div>
      <div class="mb-3">
        <label for="exampleInputPassword1" class="form-label">Gender</label>
        <input type="text" class="form-control" id="exampleInputPassword1" v-model="gender">
      </div>
      <div class="mb-3">
        <label for="exampleInputPassword1" class="form-label">Age</label>
        <input type="text" class="form-control" v-model="age">
      </div>
      <button type="submit" class="btn btn-primary" @click="register">Submit</button>
    </form>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'Questionnaire',
  props: {
    msg: String
  },
  data() {
    return {
      userName: "",
      password: "",
      email: "",
      phone: "",
      gender: "",
      age: ""
    }
  },
  methods: {
    register() {
      const headers = {"Content-Type": "application/json"};
      const data = {username: this.userName, password: this.password,
        email: this.email, phone: this.phone, gender: this.gender, age: this.age};
      axios.post("http://localhost:8080/signup/signup", data, {headers: headers})
          .then(response => window.console.log(response))
          .catch(response => window.console.log(response));
    }
  }
}
</script>

